import { Component, OnInit } from '@angular/core';
import { Http, RequestOptions } from '@angular/http';
import { Coupon } from '../../common/coupon';

@Component({
  selector: 'app-getallcoupons',
  templateUrl: './getallcoupons.component.html',
  styleUrls: ['./getallcoupons.component.css']
})
export class GetallcouponsComponent implements OnInit {
  public coupon: Coupon
  public coupons: Coupon[]
  protocol: String = "https";
  server: String = "localhost";
  port: String = "8443";
  companyApiPath = this.protocol + "://" + this.server + ":" + this.port + "/Company/";

  constructor(private _http: Http) {
    this.getAllCoupons()
  }

  ngOnInit() {
  }

  getAllCoupons() {
    return this._http.get(this.companyApiPath + "getAllCoupons").subscribe(
      (resp) => {
        this.coupons = resp.json()
      }
    )
  }

  public deleteCoupon(coupon: Coupon) {
    return this._http.delete(this.companyApiPath + "removeCoupon/" + coupon.id, new RequestOptions({ body: coupon })).subscribe(
      (resp) => {
        console.log(coupon)
        console.log(resp)

      }
    )
  }

  public updateCoupon(coupon: Coupon) {
    return this._http.put(this.companyApiPath + "updateCoupon/" + coupon.id, coupon).subscribe(
      (resp) => {
        console.log(coupon)
        console.log(resp)
      }
    )
  }

  myFunction() {
    var input, filter, table, tr, td, i;
    input = document.getElementById("search-bar");
    filter = input.value.toUpperCase();
    table = document.getElementById("companyTable");
    tr = table.getElementsByTagName("tr");
    for (i = 0; i < tr.length; i++) {
      td = tr[i].getElementsByTagName("td")[1];
      if (td) {
        if (td.innerHTML.toUpperCase().indexOf(filter) > -1) {
          tr[i].style.display = "";
        } else {
          tr[i].style.display = "none";
        }
      }
    }
  }
}
